.container {
  width: 100%;
  min-width: 320px;
  padding: 0 20px;
  margin: auto;

  .contacts & {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 34px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1050px;
    padding: 0;
    // there is no need to have padding at all :)
    // because I have set container width to 1050px and that is "0" point
    // for other sections on 1280px break point.
  }
}
